
.Control {
  display: flex;
}

.ControlInfo {
  display: flex;
  padding: 5px;
  justify-content: center;
  align-items: center;
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 16px;  /* Preferred icon size */
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  color: #b1b1b1;
  cursor: default;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  font-feature-settings: 'liga';
}

.ControlInfo .InfoTooltip{
  visibility: hidden;
  width: 200px;
  background-color: #5b5d62;
  color: #fff;
  padding: 10px 10px;
  position: absolute;
  z-index: 5;
  top: 110%;
  font-size:12px;
  font-weight: 300;
  margin-left: -110px;
  opacity: 0;
  transition: opacity 0.3s;
}

.InfoTooltip p {
  margin: 0;
}

.InfoTooltip span {
  font-weight: 600;
}

.ControlInfo .InfoTooltip::after {
  content: "";
  position: absolute;
  bottom: 100%;
  right: 0;
  border-width: 5px;
  border-style: solid;
  border-color: transparent #5B5D62 #5B5D62 transparent;
  /* border-color: #5B5D62 transparent transparent transparent; */
}

.ControlInfo:hover .InfoTooltip {
  visibility: visible;
  opacity: 1;
}

.ControlInfo:hover .material-icons{
  color:#5B5D62;
}

.KeyFigures {
  width: 45%;
  border: 1px solid #a70000;
}

.LineGraph {
  height: 500px;
  padding-top: 10px;
  box-sizing: border-box;
}

.Visualisation {
  width: 75%;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  padding: 10px;
}

@media (max-width: 752px) { 
  .Visualisation { 
      width: 100%;
      box-sizing: border-box;
  }
  .KeyFigure__Md_Lg {
      display: none;
  }
}
.Layout {
  background-color: #fef7e7;
  display: flex;
  margin: 0 auto;
  max-width: 1059px;
  height: 600px;
}


/* Large devices (desktops, 992px and up) */
@media (max-width: 752px) { 
  .Layout {
      flex-flow: column;
      width: 100%;
  }
}



.Mixer {
  width: 25%;
  padding: 10px;
}

.Sm_Md {
  display: none;
}

@media (max-width: 752px) { 
  .Mixer {
